import React, { useState } from 'react';
import {
  Box,
  FormControl,
  InputAdornment,
  IconButton,
  CircularProgress,
  Snackbar,
  OutlinedInput,
} from '@mui/material';
import Grid from '@mui/material/Grid2'
import SearchIcon from '@mui/icons-material/Search';
import api from '../../services/api';
import BookCard from '../BookCard/BookCard';
import { useAuth0 } from '@auth0/auth0-react';

const SearchBar: React.FC = () => {
  const { getAccessTokenSilently } = useAuth0();
  const [query, setQuery] = useState('');
  const [loading, setLoading] = useState(false);
  const [results, setResults] = useState<Book[]>([]);
  const [error, setError] = useState<string | null>(null);
  const [success, setSuccess] = useState<string | null>(null);

  const handleSearch = async () => {
    if (!query.trim()) return;

    setLoading(true);
    setError(null);
    try {
      const response = await api.get(`/books/search`, { params: { q: query } });
      console.log('API Response:', response.data);
      setResults(response.data.results || []);
      console.log('Search results:', response.data.results);
    } catch (error) {
      console.error('Error searching books:', error);
      setError('Failed to fetch search results. Please try again later.');
    } finally {
      setLoading(false);
    }
  };

  const handleKeyPress = (event: React.KeyboardEvent) => {
    if (event.key === 'Enter') {
      handleSearch();
    }
  };

  const handleAddToCollection = async (bookId: number) => {
    try {
      const token = await getAccessTokenSilently({
        authorizationParams : {
          audience: 'https://localhost:5000',
          scope: 'add:books',}
      });

      await api.post(
        `/books/add`,
        { bookId },
        {
          headers: {
            Authorization: `Bearer ${token}`,
          },
        }
      );

      setSuccess('Book added to your collection!');
    } catch (error) {
      console.error('Error adding book to collection:', error);
      setError('Failed to add book to your collection. Please try again.');
    }
  };

  return (
    <>
      <FormControl
        variant="outlined"
        sx={{
          m: 1,
          width: { xs: '100%', sm: '50%', md: '40%', lg: '30%' },
        }}
      >
        <OutlinedInput
          id="search-book"
          placeholder="Search for books..."
          value={query}
          onChange={(e) => setQuery(e.target.value)}
          onKeyDown={handleKeyPress}
          endAdornment={
            <InputAdornment position="end">
              <IconButton
                onClick={handleSearch}
                edge="end"
                aria-label="search"
                disabled={loading}
              >
                {loading ? <CircularProgress size={24} /> : <SearchIcon />}
              </IconButton>
            </InputAdornment>
          }
        />
      </FormControl>

      {Array.isArray(results) && results.length > 0 && (
        <Box mt={4} width="100%">
            <Grid container justifyContent="center">
                {results.map((book) => (
                    <BookCard
                        key={book.id}
                        book={book}
                        onAdd={handleAddToCollection}
                    />
                ))}
            </Grid>
        </Box>
      )}


      {error && (
        <Snackbar
          open={!!error}
          autoHideDuration={6000}
          onClose={() => setError(null)}
          message={error}
        />
      )}

      {success && (
        <Snackbar
          open={!!success}
          autoHideDuration={6000}
          onClose={() => setSuccess(null)}
          message={success}
        />
      )}
    </>
  );
};

export default SearchBar;
